/*------------------------------- Importamos Bootstrap ---------------------------*/

/*------------------------------- Estilos Generales ---------------------------*/

html{
  width: 100%;
  height: 100%;
}

body{
  width: 100%;
  /* height: 100%; */

  margin: 0px;
  padding-top: 3.5%;

  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  background: url(https://www.xtrafondos.com/wallpapers/resoluciones/19/noche-cielo-estrellas-y-cometa_1920x1080_3036.jpg);
  background-size: cover;
  background-position-y: -50px;
  background-attachment: fixed;

  display: flex;
  align-items: center;
  justify-content: center;
}

body ::selection{
  background-color: black;
  color: white;
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: #f2f2f2; 
}

::-webkit-scrollbar-thumb {
  background: #555555; 
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #242424; 
}

/*------------------------ Botones - Estilos Generales -----------------------*/

.boton{
    font-family: sans-serif;
    font-size: 1.5vw !important;
    border-radius: 5px;
    box-sizing: border-box;
    box-shadow: 0px -4px 1px rgba(0, 0, 0, 0.603) inset;
    border: none;
    font-family: Arial, Helvetica, sans-serif;
    font-weight: 500;
    padding: 5px 15px;
  }
  
.boton:hover{
    box-shadow: 0px -5px 1.5px rgba(0, 0, 0, 0.603) inset;
  }
  
 /*--------------------- Inicio de Sección - Botón ---------------------------*/
  
main button{
    background: rgba(30, 102, 197, 0.658);
}

 /*------------------------ Salir de Sección - Botón ----------------------------*/
  
header button{
    background: rgba(255, 79, 48, 0.788);
    margin-right: 2%;
}


/*----------------------- Mensajes --------------------------*/

h2.chat {
    text-align: center;
}
  
div.user {
    padding: 1%;
    box-sizing: border-box;
    margin: 2%;
    background-color: rgba(63, 160, 189, 0.699);
    border-radius: 10px;
    border: rgba(216, 213, 213, 0.377) 0.1px solid;
}

img.photo-user {
    width: 5%;
    border-radius: 50%;
    border: rgba(0, 0, 0, 0.705) 0.5px solid;
    margin-bottom: 1%;
}

span.name-user {
    color: rgb(17, 17, 17);
    margin: 1%;
    font-weight: 500;
    font-size: 1.4vw;
}

span.date-user {
    display: block;
    margin: 1%;
    float: right;
    font-size: 1vw;
    color: rgb(26, 23, 23);
    text-align: end;
    font-style: italic;
    padding: .2%;
    border-radius: 5%;
    background-color: rgba(255, 255, 255, 0.329);
}

p.mensaje-user {
    background-color: rgba(255, 248, 220, 0.719);
    border: rgba(4, 4, 124, 0.534) solid 0.5px;
    padding: 1%;
    border-radius: 8px;
    text-align: justify;
    margin: 0;
    font-size: 1.2vw;
}
/*------------------------------ Enviar Mensajes: ----------------------------------*/
div.messages-container {
  margin-bottom: 6%;
}


.form{
  width: 100%;
  height: 10%;
  box-sizing: border-box;
  background-color: rgb(37, 36, 36);
  display: flex;
  align-items: center;
  position: fixed;
  padding: .5%;
  bottom: 0%;
  border-top: 1px rgba(255, 255, 255, 0.534) solid;
}

input.mensaje {
  width: 85%;
  height: 90%;
  background-color: white;
  border-radius: 8px;
  border: black solid 1px;
  font-size: 1.2vw;
}

input.mensaje:focus {
  outline: none;
  border: black solid 2px;
}

input.mensaje::-webkit-input-placeholder {
  padding: .5%;
  bottom: 51%;
}

input.mensaje:-ms-input-placeholder {
  padding: .5%;
  bottom: 51%;
}

input.mensaje::placeholder {
  padding: .5%;
  bottom: 51%;
}

/*------------------------ Emoji - Botón ---------------------------*/

button.btn-emoji{
  background: url("https://cdn-0.emojis.wiki/emoji-pics/facebook/beaming-face-with-smiling-eyes-facebook.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center center;
  width: 5%;
  height: 90%;
  left: 75%;
  margin: 0 .5%;
  border-radius: 10%;
  border: none;
}

button.btn-emoji:hover{
  outline: 2px rgba(255, 255, 255, 0.746) ridge;
  outline-offset: 2px;
}

/*------------------------ Send - Botón ---------------------------*/


button.btn-send{
  height: 90%;
  left: 84%;
  width: 10%;
  background: rgb(68, 135, 223);
  color: black;
}

button.btn-send:hover {
  box-shadow: 0px -5px 1.5px rgba(0, 0, 0, 0.603) inset;
  cursor: pointer;
}
/*----------------------- Estilos del Logo - Infinity Chat -------------------------*/

.logo{
    width: 5%;
}

main{
    width: 65%;
    height: 50%;
    margin: 8% auto;
    border-radius: 10px;
    background: rgba(255, 255, 255, 0.692);
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding-bottom: 3%;
    box-shadow: 8px 8px 8px rgba(255, 251, 251, 0.301);
    border: 0.5px rgba(0, 0, 0, 0.616) solid;
}

main h2{
    background: rgb(27, 29, 29);
    color: white;
    width: 100%;
    border-top-left-radius: inherit;
    border-top-right-radius: inherit;
    box-sizing: border-box;
    padding: 1.5%;
    text-align: center;
    font-family: sans-serif;
    font-size: 2vw;
    font-weight: bold;
}
  
main img{
    width: 25%;
    height: auto;
    margin-bottom: 2%;
}
header.menu{
	width: 100vw;
	height: 10%;
	background-color: rgba(0, 0, 0, 0.959);
	color: white;
	display: flex;
	justify-content: space-between;
	align-items: center;
	padding: .3%;
	position: fixed;
	top: 0;
	left: 0;
	border-bottom: 1px solid rgba(255, 252, 252, 0.274);
}

div.invisible-btn {
	width: 5%;
}
.App {
	text-align: center;
}


